.nav-bar {
    position: absolute;
    top: 5%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 100;
}

.nav-bar .link-list {
    display: flex;
    flex-direction: row;
    gap: 30px;
    background-color: transparent;
    align-items: center;
    padding: 5px 10px;
    backdrop-filter: blur(3px);
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.05), 0 6px 20px 0 rgba(0, 0, 0, 0.05);
    border-radius: 40px;
}

.nav-bar .link {
    @apply text-primary-400 transition-all duration-300 select-none text-12;
    border-radius: 20px;
    padding: 5px 10px;
}

.nav-bar .link:hover:not(.selected) {
    background-color: #00000008;
}
.nav-bar .link.selected {
    background-color: #00000011;
}