.page-container {
    display: flex;
    justify-content: stretch;
    align-items: stretch;
    width: 100%;
    height: 100%;
    position: absolute;
    perspective: 1600px;
    transform-style: preserve-3d;
}

.page-sub-container.invisible-left {
    transform: rotateY(90deg) translateZ(-1200px);
    transition-delay: 0.3s;
    /* base */
}

.page-sub-container.invisible-right {
    transform: rotateY(-90deg) translateZ(-1200px);
    transition-delay: 0.3s;
    /* base */
}

.page-sub-container.invisible-right .page-content, .page-sub-container.invisible-left .page-content {
    @apply rounded-3xl;
    transform: scale(0.8, 0.8);
    transition-delay: 0s;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.2);
}

.page-sub-container {
    width: 100%;
    height: 100%;
    transition: 1s; 
    transition-delay: 0.7s;
    /* base + base / 3 */
}

.page-content {
    @apply box-border flex-grow;
    transition: 0.3s;
    transition-delay: 1.7s;
    /* base * 2.3 */
    overflow: hidden;
    width: 100%;
    height: 100%;
    transform-style: preserve-3d;
}